import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {ChequeBookServiceProvider} from "../../providers/cheque-book-service/cheque-book-service";



@IonicPage()
@Component({
  selector: 'page-issue-cheque-book02',
  templateUrl: 'issue-cheque-book02.html',
})
export class IssueChequeBook02Page {
  issueChequeBookError02;

  accountNo;
  leafDetails;
  leafCount;
  formVal = {tpin: "", otp: ""};
  tpin = "";

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public cryptoUtil: CryptoParamUtilProvider,
              public maskTokenUtil: MaskTokenProvider,
              public storage: LocalStorageProvider,
              public alertCtrl: AlertController,
              public chqBookService: ChequeBookServiceProvider,
              public tokenService: TokenServiceProvider) {

    this.accountNo = this.navParams.get("accountNo");
    this.leafDetails = JSON.parse(this.navParams.get("leafDetails"));
  }


  issueChequeBook02Action = function () {
    this.issueChequeBookError02 = "";
    let flag = true;
    this.tpin = this.formVal.tpin;

    let validateTpinExp = new RegExp("[0-9]{4}$");

    if (this.leafCount == "" || typeof(this.leafCount) == "undefined")
    {
      this.issueChequeBookError02 = "Please select no of leaf.";
      flag = false;
      return;
    }

    if (this.tpin == "" || typeof(this.tpin) == "undefined")
    {
      this.issueChequeBookError02 = "Please Enter TPIN.";
      flag = false;
      return;
    }


    if (!validateTpinExp.test(this.tpin))
    {
      this.issueChequeBookError02 = "Invalid TPIN.";
      flag = false;
      this.formVal = {tpin: ""};
      return;
    }

    if (flag) {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encSrcAccNo = this.cryptoUtil.getEncryptVal(this.accountNo);
      let encTPin = this.cryptoUtil.getEncryptVal(this.tpin);

      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.chqBookService.verifyTpinForChqBookRequisition(unMaskToken, appData.userId, appData.appId, encSrcAccNo, encTPin)
        .then(data => {
          this.onTpinVerifyComplete(data);
        });
    }
  };

  onTpinVerifyComplete = function (data) {

    if (data.statusCode == 1) {
      this.navCtrl.push('IssueChequeBook03Page', {
        'accountNo': this.accountNo,
        'leafCount': this.leafCount
      });
    }

    else if (data.statusCode == 0) {
      this.issueChequeBookError02 = data.note;
    }

    else {
      this.issueChequeBookError02 = "System Error Contact with Administrator.";
    }
  };

  ionViewDidLoad() {

  }

}



